import React from 'react';
import { exists } from '../utils/utils';
import { ThymeleafParamName, getThymeleafParam, getThymeleafParamBool } from '../utils/thymeleaf-utils';
import RedirectButton from './RedirectButton';

export function createRedirectButton() {
	const isRegisteredThParamName = ThymeleafParamName.IS_REGISTERED;
	const isRegistered = getThymeleafParamBool(isRegisteredThParamName);
	let urlThParamName;
	let buttonTextThParamName;
	if (isRegistered) {
		urlThParamName = ThymeleafParamName.DIALOG_URL;
		buttonTextThParamName = ThymeleafParamName.DIALOG_REDIRECT_BUTTON_TEXT;
	} else {
		urlThParamName = ThymeleafParamName.GROUP_URL;
		buttonTextThParamName = ThymeleafParamName.GROUP_REDIRECT_BUTTON_TEXT;
	}

	const url = getThymeleafParam(urlThParamName);
	const buttonText = getThymeleafParam(buttonTextThParamName);
	if (!exists(url) || !exists(buttonText)) {
		return null;
	}

	const isMobileThParamName = ThymeleafParamName.IS_MOBILE;
	const isMobile = getThymeleafParamBool(isMobileThParamName);
	return (
		<RedirectButton text={buttonText} href={url} openInNewTab={!isMobile} />
	);
}