import React from 'react';

import styles from './style.module.css';
import { getThymeleafParamBool, ThymeleafParamName } from '../utils/thymeleaf-utils';
import { exists } from '../utils/utils';

const Message = ({ marginTopPc, marginTopMobile, textHeader, emoticon, text, buttons }) => {
	const isMobileThParamName = ThymeleafParamName.IS_MOBILE;
	const isMobile = getThymeleafParamBool(isMobileThParamName);
	const marginTop = isMobile === true ? marginTopMobile : marginTopPc;
	return (
		<div style={{ marginTop: (marginTop) }}>
			<div className={styles.textContainer}>
				<div className={styles.textHeader}>{textHeader}</div>
				<img className={styles.emoticon} src={emoticon} alt="" />
				<p className={styles.text}>{text}</p>
			</div>
			{
				buttons.map((button) => {
					if (!exists(button)) {
						return;
					}
					return (button);
				})
			}
		</div>
	);
};

export default Message;